$pink: #ed1e79;
$purple: #7435b5;
$orange: #f05b2f;
$blueLight: #00ffff;
$blue: #0068ff;
$blueDark: #1947db;
$green: #7ac943;
$gray: #e9e9e9;

.App {
  padding: 100px;
  .wrap {
    margin: 0px;
    min-height: 240px;
    width: 0;
    min-width: 650px;
    background-color: #161616;
    border-radius: 12px;
    padding: 24px 36px;
    color: $gray;
    // font-family: 'Cousine', monospace;
    // font-family: 'Inconsolata', monospace;
    font-family: 'Consolas', monospace;
    // font-family: 'Menlo', monospace;
    line-height: 2;
    font-weight: 900;
    font-size: 26px;
  }
  .simple {
    font-weight: 400;
    font-size: 22px;
  }
}